.bookInfoBlock {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;

  &__imageContainer {
    padding: 0 100px;

    box-sizing: border-box;

    display: flex;
    justify-content: center;
    align-items: center;


    height: 700px;

    background: aliceblue;
    
    @media screen and (max-width: 1280px) {
      padding: 0 20px;
      margin: 0 20px;
      width: 100%;
    }

    &Image {
      min-height: 400px;
      min-width: 350px;
      max-width: 350px;

      object-fit: contain;

      cursor: pointer;

      transition: .3s linear;

      box-shadow: 5px 15px 15px 2px rgba(0,0,0,.5);

      background: white;

      @media screen and (max-width: 450px) {
        & {
          min-width: 200px;
          max-width: 230px;
        }
      }

      &:hover {
        transform: scale(1.2);
      }
    }

    &NotImageIcon {
      width: 75px;
      height: 75px;
    }
  }
}