.mainPage {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  padding: 0 0 150px;

  min-height: 300px;

  &__startFindMessage {
    font-size: 32px;
    font-weight: 700;

    color: #333333;
  }

  &__foundCount {
    margin: 20px 0 50px;

    font-size: 32px;
    font-weight: 700;

    @media screen and (max-width: 425px) {
      & {
        font-size: 24px;
      }
    }
  }

  &__loadMoreBtn {
    margin-top: 50px;
  }

  &__loader {
    width: 100px;
    height: 100px;
  }
}